@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container-header {
  background: #fff;
  padding: 0 30px;
}

.container-header .content-header {
  height: 9vh;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-header .content-header nav {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.container-header .content-header nav div,
.container-header .content-header nav p {
  font-weight: bold;
  color: #81259d;
  display: inline;
  margin: 0 10px;
  font-size: 2rem;
}

.container-header .content-header nav p {
  font-size: 1.2rem;
}

@media (max-width: 600px) {
  .container-header {
    margin-bottom: 15px;
  }

  .container-header .content-header nav p {
    font-size: 14px;
  }
}

@media (max-width: 400px) {
  .container-header .content-header nav p {
    font-size: 10px;
  }
}

.grid {
  padding: 10px;
  display: grid;
  border-radius: radius 3px;
}

.grid .header {
  display: grid;
  grid-template-columns: 45% 45% 10%;
  height: 30px;
  padding: 5px;
  font-weight: 700;
  border-bottom: 1px solid;
  border-bottom-color: rgba(0, 0, 0, 0.1);
  color: #81259d;
}

@media (max-width: 600px) {
  .grid .header {
    display: none;
  }
}

.grid .items {
  display: grid;
  grid-template-columns: 45% 45% 10%;
  grid-row-gap: 10px;
  padding: 10px;
  font-size: 16px;
}

@media (max-width: 600px) {
  .grid .items {
    display: flex;
    flex-direction: column;
    line-height: 2em;
  }
}

.grid .items input {
  height: 30px;
  width: 70%;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.popup-content {
  border: 0;
  border-radius: 10px;
}

header {
  height: 80px;
  width: 100%;
  text-align: center;
  font-size: 2rem;
  font-weight: 600;
  color: #81259d;
}

ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}

ul div,
ul .last {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 500;
  color: #81259d;
}

ul div:last-child svg {
  display: none;
}

.grid-modal {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid-modal .title {
  font-size: 1.3rem;
  font-weight: 700;
  margin-right: 15px;
  color: #81259d;
}

hr {
  margin: 25px 15px;
}

.grid-modal .value {
  font-size: 1.2em;
  font-weight: 700;
  color: #81259d;
  margin-right: 50px;
}

.control {
  height: 50px;
  display: flex;
  float: right;
  align-items: center;
  margin: 5px;
}

.control button {
  height: 36px;
  width: 100px;
  background: transparent;
  border: 1px solid #81259d;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: 600;
  color: #81259d;
  cursor: pointer;
}

.control button:hover {
  color: #fff;
  border: 1px solid #81259d;
  background: #81259d;
}

.container {
  height: 90vh;
  max-width: 800px;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.container .register {
  height: 100px;
  width: 100%;
  background: #fff;
  border-radius: 8px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.container input {
  background: #fff;
  border: 0;
  height: 40px;
  width: 70%;
  padding: 0 5px;
  color: #455463;
  margin: 0 10px;
  border-bottom: 1px solid #455463;
  border-bottom-style: dotted;
  font-size: 18px;
  z-index: 2;
}

.container input:placeholder {
  color: #455463;
}

.container .register button {
  display: flex;
  align-items: center;
  padding: 10px;
  margin: 0 15px;
  background: #81259d;
  color: #fff;
  border: 1px solid #81259d;
  border-radius: 4px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
}

.container .register button:hover {
  background: #fff;
  color: #81259d;
}

.content {
  width: 100%;
  background-color: #fff;
  border-radius: 8px;
}

.content .controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content .controls .city-selected {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.content .controls .generation input {
  width: 100px;
}

@media (max-width: 600px) {
  .content .controls {
    flex-direction: column;
  }

  .content .controls .select {
    width: 90%;
  }

  .content .controls a {
    width: 100%;
  }

  .content .controls a button {
    width: 90%;
    margin: 0 auto;
  }
}

.content .controls .select {
  background: #fff;
  border: 0;
  height: 40px;
  padding: 0 5px;
  color: #81259d;
  margin: 20px 15px;
  border: 1px solid #81259d;
  border-radius: 3px;
  font-size: 16px;
  cursor: pointer;
}

.content .controls a {
  text-decoration: none;
}

.container .btn-execute {
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 0 15px;
  background: transparent;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
}

.container .btn-execute:hover {
  background: #fff;
  color: #81259d;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  overflow: auto;
  background: -webkit-gradient(linear, right top, left top, from(#272142), to(#81259d));
  background: linear-gradient(-90deg, #272142, #81259d);
  -webkit-font-smoothing: antialiased;
}

body,
input,
button {
  font-family: Roboto, sans-serif;
}

.Toastify__toast {
  width: 100%;
  font-weight: bold;
  border-radius: 8px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

